import React,{useState,useEffect,useContext} from 'react'
import { AuthContext } from '../Context/AuthProvider'
import { storage } from "../firebase";
import { database } from "../firebase";
import firebase from "../firebase";
import img from '../Components/TM-Instagram.png'
import "./css/Signup.css";
import { useHistory } from 'react-router-dom';

function Signup() {
    const [Email, setEmail] = useState('');
    const [Password, setPassword] = useState('');
    const [Error, setError] = useState("")
    const [Name, setName] = useState('');
    const [Loading, setLoading] = useState(false);
    const [File, setFile] = useState(null)
    const {signup} =useContext(AuthContext);
    const {logout,currentuser} =useContext(AuthContext);
    const history = useHistory();
    const handleSignup=async (e)=>{
        
         e.preventDefault();
         try{
        setLoading(true);
        let res=await signup(Email,Password);
        let uid=res.user.uid;
        console.log(uid);
        const uploadtask=storage.ref('/users/'+uid+'/profileImage').put(File);
        // Register three observers:
// 1. 'state_changed' observer, called any time the state changes
// 2. Error observer, called on failure
// 3. Completion observer, called on successful completion
uploadtask.on('state_changed',fn1,fn2,fn3);
function  fn1(snapshot) {
    var progress = (snapshot.bytesTransferred / snapshot.totalBytes) * 100;
    console.log('Upload is ' + progress + '% done');
    switch (snapshot.state) {
      case firebase.storage.TaskState.PAUSED: // or 'paused'
        console.log('Upload is paused');
        break;
      case firebase.storage.TaskState.RUNNING: // or 'running'
        console.log('Upload is running');
        break;
}
}
function  fn2(error) {
    setError(error);
    setTimeout(() => {
        setError("");
    }, 2000);
}
async function fn3(){
    let downloadurl=await uploadtask.snapshot.ref.getDownloadURL();
    console.log(downloadurl);
    console.log("start")
    await database.users.doc(uid).set({
        email:Email,
        password:Password,
        userId:uid,
        username:Name,
        createdAt:database.getCurrentTimeStamp(),
        profileUrl:downloadurl,
        postIds:[]
    })
    history.push("/")
 
    console.log("end")
}
setLoading(false);
console.log("signed up")
         }
         catch(e){
            setError(e);
            setTimeout(() => {
                setError("");
            }, 2000);
            setLoading(false);
         }
 }
 
    const handlefilesubmit=(e)=>{
        let file=e.target.files[0];
        console.log(file);
        if(file!=null){
            setFile(file);
        }
    }
    const handlesignout=async (e)=>{
        
        e.preventDefault();
        try{
              setLoading(true);
        let res=await logout(Email,Password);
        console.log(res)
        }
        catch(e){
           console.log(e.message);
        }
    }
    useEffect(()=>{
        if(currentuser)
          {
            history.push("/")
          }
      })
    return (
        <div className='Maincontainer'>
        <div className="Main1">
            <div className="Iphone">
                <div className="camandmic">
                    <div className="dot"></div>
                    <div className="speak1">
                    <div className="cam"></div>
                        <div className="speak"></div>
                        
                    </div>
                </div>
                <div className="Display">
                    <div className="dis">
                    <img className="Homepage" src={img}></img>
                    </div>
                </div>
                <div className="button">
                    <div className="bt"></div>
                </div>
            </div>
        </div>
        <div className='signupdet'>
            <div className="instadiv">
            <i className="fab fa-instagram insta"></i>
           </div>
            <form onSubmit={handleSignup}>
                <div >
                  <input className="Box" type='text' value={Name} placeholder="Username" onChange={(e)=>setName(e.target.value)}></input>
                </div>  
                <div>
            
                    <input className="Box" type='email' value={Email} placeholder="Email" onChange={(e)=>setEmail(e.target.value)}></input>
                </div> 
                <div>
                   <input className="Box" type='password' value={Password} placeholder="Password" onChange={(e)=>setPassword(e.target.value)}></input>
                </div> 
                <div>
    
                <input type="file" accept="image/*" onChange={handlefilesubmit}></input>
                </div> 
                <button className='signbtn' type='submit' disabled={Loading}>Signup</button>
                
            </form>
            <button className='signout' onClick={handlesignout} disabled={Loading}>signout</button>
        </div>
        </div>


    )
}

export default React.memo(Signup)
